import { FormControl } from '@angular/forms';

export class AztecaValiators {

    static selected(c: FormControl): any {
        if (c.value > 0)
            return null;
        else
            return { error: "Valor no seleccionado" };
    }

    static validFile(c: FormControl): any {
        if (c.value && (c.value.Content != undefined || c.value.FileUri != ''))
            return null
        else
            return { error: "Archivo no seleccionado" };
    }

}

